<template>
    <v-row justify="center">
        <v-btn
            class="ma-4"
            icon
            @click.stop="dialog = true"
        >
            <v-icon>mdi-exit-to-app</v-icon>
        </v-btn> 
        <v-dialog
            v-model="dialog"
            max-width="350"
        >
            <v-card>
                <v-card-title>¿seguro?</v-card-title>
                <v-card-text>¿seguro?</v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        text
                        @click="exit()"
                    >
                        salir
                    </v-btn>

                    <v-btn
                        text
                        @click="dialog = false"
                    >
                        quedarse
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
import { mapActions } from "vuex"

export default {
    data () {
        return {
            dialog: false,
        }
    },
    methods:{
        ...mapActions('app',['logout']),     
        exit(){
            this.dialog = false
            this.logout()
            this.$emit('onExit')
        }
    }
}
</script>