import Vue from 'vue'
import Vuex from 'vuex'

// import db from '@/services/database.service'
import ApiService from '@/services/api.service'

Vue.use(Vuex)

export default {
    namespaced: true,
    // -----------------------------------------------------------------
    state: {
        viewMode: {
            index: 0,
            availableModes: [
                {icon: 'mdi-view-agenda-outline', component:'McExtended'},
                {icon: 'mdi-calendar', component:'McCalendar'},
                {icon: 'mdi-view-grid', component:'McCompact'},
            ]
        }
    },
    // -----------------------------------------------------------------
    getters: {
        getAuthenticated(state, getters, rootState) {
            return rootState.auth.accessToken
        },
        getViewMode(state){
            return state.viewMode.availableModes[state.viewMode.index]
        }
    },
    // -----------------------------------------------------------------
    mutations: {
        nextViewMode(state){
            const current = state.viewMode.index + 1
            state.viewMode.index = current % state.viewMode.availableModes.length
        }
    },
    // -----------------------------------------------------------------
    actions: {
        async login({ commit }, payload) {
            /* eslint-disable no-console */
            console.log('login ...', commit)
            /* eslint-enable no-console */
            await this.dispatch('auth/login', payload)
            await this.dispatch('app/initApp')
        },
        async logout() {
            /* eslint-disable no-console */
            console.log('logout ...')
            /* eslint-enable no-console */
            await this.dispatch('auth/logout')
        },
        async initApp({ commit }) {
            /* eslint-disable no-console */
            console.log('starting ...', commit)
            await this.dispatch('app/test')
            /* eslint-enable no-console */
        },
        async test({ commit }) {
            /* eslint-disable no-console */
            console.log('test', commit)
            const response = await ApiService.get("/api/auth/test")
                .catch((e) => {
                    const errorMsg = JSON.stringify(e.response.data)
                    console.log('errorMsg', errorMsg)
                })

            if (response) {
                console.log('fetching data', response)
                if (response.data.success) {
                    return response.data.data
                }
            }
            /* eslint-enable no-console */
        },
        async getDays() {
            /* eslint-disable no-console */
            console.log('getting days ....')
            /* eslint-enable no-console */
            return [
                { day: '2020-01-01'},
                { day: '2020-01-02'},
                { day: '2020-01-03'},
            ]
        }
    }
}