<template>
    <v-form
        ref="form"
        lazy-validation
    >
        <v-text-field
            v-model="form.email"
            label="email"
            required
            :rules="emailRules"
        ></v-text-field>
        <v-text-field
            v-model="form.password"
            label="password"
            required
            :rules="passwordRules"
        ></v-text-field>
        <v-btn
          class="mr-4"
          @click.prevent="onSubmit()"
        >
            login
        </v-btn>
    </v-form>
</template>
<script>
import { mapActions } from "vuex";

export default {
    name: "login",
    data() {
      return {
            form: {
                email: "a@a.com",
                password: "",
            },
            valid: false,
            emailRules: [
                v => !!v || "E-mail is required",
                v => /.+@.+\..+/.test(v) || "E-mail must be valid"
            ],
            passwordRules: [v => !!v || "Password is required"]
        }
    },
    methods:{
        ...mapActions('app',['login']),
        onSubmit(){
            if(this.$refs.form.validate()){
                this.login({ email: this.form.email, password: this.form.password });            
            }
        }
    }
}

</script>